const {ccclass, property} = cc._decorator;

@ccclass
export default class ErmjBaseComponent extends cc.Component {
    protected getNodeComponent<T extends cc.Component>(path:string, type: {prototype: T}): T;
    protected getNodeComponent(path:string, type: any): any{
        return Global.UIHelper.getComponent(this.node, path, type);
    }

    protected getChild(path:string): cc.Node{
        return Global.UIHelper.getChild(this.node, path);
    }
}
